import React, { useState } from "react";
import { Link } from "react-router-dom";
import "./Header.css";

function Header() {
  const [onMove, setOnMove] = useState(false);

  function logoAnimation(event) {
    if (!onMove) {
      setOnMove(true);

      const element = event.target;
      const animationType = parseInt((Math.random() * 1000) % 10) % 3;
      function endAnimation() {
        element.className = "logo";
      }

      if (animationType) {
        if (animationType === 1) {
          element.className = "logo slideOutLeft";
          element.addEventListener("animationend", endAnimation);
        } else {
          element.className = "logo hinge";
          element.addEventListener("animationend", endAnimation);
        }

        setTimeout(() => {
          element.className = "logo zoomInDown";
          element.addEventListener("animationend", endAnimation);
        }, 900);
      } else {
        element.className = "logo bounce";
        element.addEventListener("animationend", endAnimation);
      }

      setOnMove(false);
    }
  }

  return (
    <div className="Header">
      <div className="Header__styling">
        <img src="./myLogo_transp.png" alt="logo" onClick={logoAnimation} />
      </div>
      <Link to="/" className="Header__word">
        Home
      </Link>
      <Link to="/profile" className="Header__word">
        Profile
      </Link>
      <Link to="/articles" className="Header__word">
        Articles
      </Link>
      <Link to="/archives" className="Header__word">
        Archives
      </Link>
      <div className="Header__styling--Reverse" />
    </div>
  );
}

export default Header;
