import React from "react";
import SEO from "components/SEO/seo";
import "./NotFound.css";

function NotFound() {
  return (
    <div>
      <SEO
        description="페이지를 찾을 수 없습니다."
        title="Page Not Found"
        siteTitle="웅's 블로그"
      />

      <main className="NotFound">Page Not Found</main>
    </div>
  );
}

export default NotFound;
