import React, { useContext, useEffect, useState } from "react";
import SEO from "components/SEO/seo";
import { store } from "store/store";
import "./ArticleDetail.css";
import ArticleCategory from "components/Articles/Articles__Category";
import MarkdownRenderer from "../components/Markdown/MarkdownRenderer";

function ArticleDetail({ match }) {
  // URL 정보
  const category = match.params.category;
  const articleId = match.params.articleId;

  // 전역 상태 관리 (store)
  const globalState = useContext(store);
  const { value, dispatch } = globalState;
  const { articles } = value;

  // Loading
  const [isLoading, setIsLoading] = useState(true);

  // Markdown file
  const [markdown, setMarkdown] = useState("");
  const [documentTitle, setDocumentTitle] = useState("");
  const [path, setPath] = useState("");

  useEffect(() => {
    dispatch({ type: "GET_ARTICLES" });
  }, [dispatch]);

  useEffect(() => {
    // 파일 주소 찾기
    if (articles.categoryList) {
      articles.categoryList.forEach(categoryElement => {
        if (categoryElement.category === category) {
          categoryElement.itemList.forEach(articleElement => {
            if (articleElement.id === parseInt(articleId)) {
              setPath(articleElement.content);
              setDocumentTitle(articleElement.title);
            }
          });
        }
      });
    }
  }, [articles.categoryList, articleId, category]);

  useEffect(() => {
    if (path) {
      const readmePath = require(`store/article_data/${path.split("/")[2]}/${
        path.split("/")[3]
      }`);

      // 파일 가져오기
      fetch(readmePath.default)
        .then(r => r.text())
        .then(text => {
          setMarkdown(text);
        });
      setIsLoading(false);
    }
  }, [markdown, path]);

  useEffect(() => {
    dispatch({ type: "GET_ARTICLES" });
    setIsLoading(false);
  }, [dispatch]);

  return (
    <div>
      <SEO
        description={markdown}
        title={documentTitle}
        siteTitle="웅's 블로그"
      />

      <main>
        {isLoading ? (
          <div className="loader">
            <span className="loader_text">Loading...</span>
          </div>
        ) : (
          <div className="ArticleDetail">
            {/* 왼쪽 */}
            <div className="ArticleDetail__left">
              <ArticleCategory />
            </div>
            {/* 오른쪽 */}
            <div className="ArticleDetail__right">
              <MarkdownRenderer
                documentTitle={documentTitle}
                markdown={markdown}
              />
            </div>
          </div>
        )}
      </main>
    </div>
  );
}

export default ArticleDetail;
