import React, { useContext, useEffect, useState } from "react";
import SEO from "components/SEO/seo";
import ArchivedItem from "../components/Archives/ArchivedItem";
import "./Archives.css";
import { store } from "../store/store";

function Archives() {
  // 전역 상태 관리 (store)
  const globalState = useContext(store);
  const { value, dispatch } = globalState;
  const { archives } = value;

  // Loading
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    dispatch({ type: "GET_ARCHIVES" });
    setIsLoading(false);
  }, [dispatch]);

  return (
    <div>
      <SEO
        description="Github 블로그 개발기, 농구 픽업게임 플랫폼 BITZ 개발 프로젝트, 인턴 경험 등을 정리해 놓은 아카이브 입니다."
        title="Archives"
        siteTitle="웅's 블로그"
      />

      <main>
        {isLoading ? (
          <div className="loader">
            <span className="loader_text">Loading...</span>
          </div>
        ) : (
          <div className="Archives">
            <h1>Archives</h1>
            <div className="Archives__List">
              {archives.map((archive, idx) => (
                <div key={idx}>
                  <ArchivedItem archive={archive} />
                </div>
              ))}
            </div>
          </div>
        )}
      </main>
    </div>
  );
}

export default Archives;
